.Search {
    position: fixed;
    left: 0px;
    top: 0px;
    z-index: 1000;
    width: 100%;
    background-color: rgb(255, 133, 42);
}

.header-bd {
    padding-right: 9px;
    padding-left: 40px;
    padding-top: 6px;
    padding-bottom: 6px;
    background-size: 27px 20px;
    background-image: url(https://ss3.bdstatic.com/70cFv8Sh_Q1YnxGkpoWK1HF6hhy/it/u=1890136508,1817052145&fm=26&gp=0.jpg);
    background-repeat: no-repeat;
    background-position: 9px center;
}

.Search .header-bd .placeholder {
    display: block;
    font-size: 0px;
    background-color: rgb(255, 78, 34);
    text-align: center;
    border-radius: 4px;
}

a {
    text-decoration: none;
}

.text {
    height: 25px;
    line-height: 25px;
    font-size: 13px;
    display: inline-block;
    color: rgb(240, 224, 220);
}

.text::before {
    font-size: 15px;
    display: inline-block;
    color: rgb(255, 255, 255);
    margin-right: 5px;
}

.goods {
    width: 100%;
    display: -webkit-flex;
    display: flex;
    position: relative;
    box-sizing: border-box;
    place-content: flex-start space-between;
    overflow: hidden;
    flex-wrap: wrap;
    padding: 0 8px;
    background: #f2f2f2;
    padding-bottom: 13px;
    margin-bottom: 30px;
}

.details {
    height: 240px;
    position: relative;
    width: 45%;
    background: #fff;
    margin-top: 9px;
    border-radius: 6px;
    overflow: hidden;
    margin-left: 4px;
    margin-right: 4px;
    text-align: center;
}

.details img {
    width: 150px;
    height: 150px;
}

.p1 {
    width: 100%;
    height: 15px;
    font-size: 15px;
    text-align: center;
}

.p2 {
    width: 47%;
    height: 15px;
    line-height: 15px;
    float: left;
    margin-right: 0;
    color: rgb(255, 133, 42)
}

.p3 {
    width: 47%;
    height: 15px;
    line-height: 15px;
    float: left;
}